.bite,
.green {
  margin: 0;
}
.greenBiteTxtContainer {
  line-break: anywhere;
  width: 100%;
}
.greenBite {
  margin: 0;
  height: 57px;
  width: 152px;
  position: relative;
  font-size: inherit;
  line-height: 24px;
  font-weight: 600;
  font-family: inherit;
  display: flex;
  align-items: center;
  flex-shrink: 0;
}
.figureLink {
  flex: 1;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  max-width: 324px;
}
.home {
  position: relative;
  letter-spacing: 1.2px;
  line-height: 23px;
  text-transform: uppercase;
  display: inline-block;
  min-width: 45px;
  max-width: 127.5px;
}
.inputFilter,
.splitter {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-mini) 0 0;
  font-size: var(--wwwimperfectfoodscom-montserrat-bold-12-underline-size);
}
.inputFilter {
  width: 427px;
  flex-direction: row;
  padding: 0 var(--padding-16xl) 0 0;
  box-sizing: border-box;
  gap: var(--gap-4xl);
  max-width: 100%;
  font-size: var(--font-size-11xl);
}
.about {
  position: relative;
  letter-spacing: 1.2px;
  line-height: 23px;
  text-transform: uppercase;
  display: inline-block;
  min-width: 50px;
  max-width: 127.5px;
}
.conditionChecker {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-mini) var(--padding-9xl) 0 0;
}
.ourFood {
  position: relative;
  letter-spacing: 1.2px;
  line-height: 23px;
  text-transform: uppercase;
  display: inline-block;
  min-width: 79px;
  max-width: 127.5px;
  white-space: nowrap;
}
.conditionChecker1 {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-mini) 0 0;
}
.contactUs,
.symbol {
  position: relative;
  letter-spacing: 1.2px;
  display: inline-block;
}
.contactUs {
  line-height: 23px;
  text-transform: uppercase;
  min-width: 91px;
  max-width: 127.5px;
  white-space: nowrap;
}
.symbol {
  width: 11px;
  line-height: 26px;
  min-width: 11px;
}
.ifaFacebook {
  height: 25px;
  border-radius: var(--br-6xl);
  background-color: var(--eatwellecom-apple);
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: flex-start;
  padding: 0 var(--padding-6xs) 0;
  box-sizing: border-box;
}
.symbol1 {
  width: 11px;
  position: relative;
  letter-spacing: 1.2px;
  line-height: 26px;
  display: inline-block;
  min-width: 11px;
}
.divcolXs12,
.ifaInstagram {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  box-sizing: border-box;
}
.ifaInstagram {
  height: 25px;
  border-radius: var(--br-6xl);
  background-color: var(--eatwellecom-apple);
  align-items: flex-end;
  padding: 0 6.7999999999999545px 0 7.2000000000000455px;
}
.divcolXs12 {
  flex: 1;
  align-items: flex-start;
  padding: 0 var(--padding-xl);
  gap: var(--gap-mini-5);
  min-height: 1px;
  text-align: center;
  color: var(--eatwellecom-nero);
  font-family: var(--font-font-awesome-5-brands);
}
.sequenceController {
  align-self: stretch;
  flex-direction: row;
  gap: var(--gap-39xl-3);
}
.divtopFooter,
.sequenceController,
.transformer {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.transformer {
  width: 373.5px;
  flex-direction: column;
  padding: var(--padding-mini) 0 0;
  box-sizing: border-box;
  max-width: 100%;
}
.divtopFooter {
  background-color: var(--color-whitesmoke);
  flex-direction: row;
  padding: var(--padding-21xl) var(--padding-130xl) var(--padding-40xl)
    var(--padding-145xl-5);
  gap: var(--gap-50xl-2);
  flex-shrink: 0;
  debug_commit: f6aba90;
  text-align: left;
  font-size: var(--wwwimperfectfoodscom-montserrat-bold-12-underline-size);
  color: var(--eatwellecom-fern);
  font-family: var(--eatwellecom-montserrat-bold-22-upper);
}
@media screen and (max-width: 1050px) {
  .divcolXs12 {
    display: none;
  }
}
@media screen and (max-width: 750px) {
  .divtopFooter {
    gap: 69.2px 35px;
    padding-left: var(--padding-63xl);
    padding-right: var(--padding-55xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 450px) {
  .sequenceController {
    gap: 58.3px 29px;
  }
  .divtopFooter {
    gap: 69.2px 17px;
    padding-left: var(--padding-xl);
    padding-right: var(--padding-xl);
    box-sizing: border-box;
  }
}
