.divrow {
  width: 1180px;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: center;
  padding: 0 var(--padding-mini) var(--padding-5xl);
  gap: 30px 29.1px;
}
.dataHolder,
.dataInspector,
.divrow {
  display: flex;
  box-sizing: border-box;
  max-width: 100%;
}
.dataInspector {
  flex: 1;
  overflow: hidden;
  flex-direction: column;
  align-items: flex-end;
  justify-content: center;
  padding: 0 var(--padding-119xl);
}
.dataHolder {
  align-self: stretch;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 0 0 var(--padding-7xs);
  text-align: center;
  font-size: var(--font-size-mini);
  color: var(--eatwellecom-tundora);
  font-family: var(--eatwellecom-montserrat-bold-22-upper);
}
@media screen and (max-width: 1200px) {
  .dataInspector {
    padding-left: var(--padding-50xl);
    padding-right: var(--padding-50xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 750px) {
  .dataInspector {
    padding-left: var(--padding-15xl);
    padding-right: var(--padding-15xl);
    box-sizing: border-box;
  }
}
