.inputProcessor {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 0 86px;
  box-sizing: border-box;
  gap: 78px;
  max-width: 100%;
}
.divhustlePopupContent {
  width: 100%;
  flex: 1;
  overflow: hidden;
  display: none;
  max-width: 600px;
  max-height: 600px;
}
.frameParent,
.greenbite {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: flex-start;
}
.frameParent {
  align-self: stretch;
  height: 584px;
  padding: 0 0 158px;
  box-sizing: border-box;
  gap: 31px;
  max-width: 100%;
}
.greenbite {
  width: 100%;
  position: relative;
  background-color: var(--eatwellecom-nero);
  overflow: hidden;
  gap: 573px;
  letter-spacing: normal;
}
@media screen and (max-width: 1200px) {
  .inputProcessor {
    padding-bottom: 56px;
    box-sizing: border-box;
  }
}
@media screen and (max-width: 1050px) {
  .frameParent {
    height: auto;
    padding-bottom: var(--padding-84xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 750px) {
  .inputProcessor {
    gap: 39px 78px;
    padding-bottom: var(--padding-17xl);
    box-sizing: border-box;
  }
  .divhustlePopupContent {
    max-width: 100%;
  }
  .frameParent {
    gap: 15px 31px;
  }
  .greenbite {
    gap: 286px 573px;
  }
}
@media screen and (max-width: 450px) {
  .inputProcessor {
    gap: 19px 78px;
  }
  .frameParent {
    padding-bottom: 67px;
    box-sizing: border-box;
  }
  .greenbite {
    gap: 143px 573px;
  }
}
