.glutenFreepngIcon {
  width: 120px;
  height: 120px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
  object-fit: cover;
  max-width: 166.66000366210938px;
}
.p {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 0 var(--padding-4xl);
}
.free,
.mostlyGluten {
  margin: 0;
}
.mostlyGlutenFreeContainer {
  width: 100%;
  height: 36px;
  position: relative;
  letter-spacing: 1.5px;
  line-height: 18px;
  text-transform: uppercase;
  font-weight: 500;
  display: flex;
  align-items: center;
  max-width: 166.66000366210938px;
}
.divfeatureItemWrap,
.heading5 {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
.heading5 {
  align-self: stretch;
  align-items: center;
  padding: 0 var(--padding-10xs) 0 var(--padding-7xs);
}
.divfeatureItemWrap {
  height: 204px;
  flex: 1;
  align-items: flex-start;
  gap: var(--gap-29xl);
  min-width: 66px;
  text-align: center;
  font-size: var(--font-size-mini);
  color: var(--eatwellecom-tundora);
  font-family: var(--eatwellecom-montserrat-bold-22-upper);
}
